import { observable, computed } from "mobx"
import { onError } from "mobx-react"

class Store {
    @observable products = [];
    @observable cart = [];

    @computed get allProducts() {
        return this.products;
    }

    @computed get productsInCart() {
        return this.cart;
    }

    @computed get cartAmount() {
        let amount;

        if (!this.cart.length) {
            amount = 0
        } else {
            amount = this.cart
                .map(item => item.quantity)
                .reduce((prev, curr) => prev + curr)
        }

        console.log('Le amount', amount);

        return amount
    }

    setProducts(array) {
        this.products = array || [];
    }

    checkIfItemExists() {

    }

    updateOrder() {

    }

    persistCart(cart) {
        console.log(cart);
        this.cart = cart;
    }

    // add a single obj
    addToCart(item, quantity) {
        this.cart.push({
            id: item.id,
            quantity
        })

    }
}

// log mobx errors
onError(error => {
    console.log(error)
});

// export singleton store
export const store = new Store();
